@import "./variables.scss";

* {
  font-family: $font-family_1;
  margin: 0;
}
a {
  text-decoration: none;
  color: $color_3;
  &:hover {
    text-decoration: underline;
  }
}
@media (max-width: 399px) {
  * {
    display: none;
  }
}

.app {
  max-width: 1440px;
  width: 100%;
  margin: auto;
}
